import type { FC, ReactNode } from 'react'
import kebabCase from 'lodash-es/kebabCase'
import deepContainsTruthy from '@/hooks/useForm/utils/deepContainsTruthy'
import { cnx } from '@utils/ui'
import CLASS_NAME from './style'

type DefaultValue = string | null
type DefaultError = boolean | string | null | undefined

export interface FormFieldProps<V = DefaultValue, E = DefaultError> {
  dataTestId?: string
  disabled?: boolean
  /**
   * DD-NOTE: Temporary solution -> move to boolean | null
   * It is the task of HOC to transform uknown error to boolean.
   * But there are a lot of legacy components for now.
   * After all migrated this to be refactored.
   */
  error?: E
  id: string
  value: V
}

export interface FormFieldHOCProps<V = DefaultValue, E = DefaultError> extends FormFieldProps<V, E> {
  className?: string
  dataTestId?: string
  disabled?: boolean
  errorMessage?: ReactNode
  error?: E
  showError?: boolean
  helper?: ReactNode
  id: string
  touched?: boolean | null
  value: V
}

type PropsToOverride = 'id' | 'errorMessage' | 'helper' | 'touched' | 'value' | 'error'

export type FormCompatible<TProps, V = DefaultValue, E = DefaultError> = Omit<TProps, PropsToOverride> & {
  id: string
  error?: E
  errorMessage?: ReactNode
  showError?: boolean // gives ability to override error visibility per field basis
  helper?: ReactNode
  touched?: boolean | null
  value: V
}

/**
 * Adds general form control wrapper over the Component and renders error or heper text inside the wrapper
 * @param Comp component to render
 * @returns enhanced component
 */
function FormFieldHOC<THOC extends FormFieldHOCProps<V, E>, V = DefaultValue, E = DefaultError> (
  Comp: FC<FormFieldProps<V, E>>
): FC<THOC> {
  return (props: THOC): JSX.Element => {
    const {
      error, touched, disabled,
      showError: _showError,
      className,
      helper = null,
      dataTestId: _dataTestId,
      errorMessage = (typeof error === 'string' ? error : null),
      ...otherProps
    } = props

    /**
     * we need to convert touched to boolean directly because of legacy code it might be not boolean at all.
     * It might be {} for true for example.
     */
    const showError = _showError ?? (disabled !== true && Boolean(touched) && deepContainsTruthy(error))
    const dataTestId = _dataTestId ?? kebabCase(otherProps.id)
    const hasErrorMessage = errorMessage !== '' && errorMessage != null

    return (
      /**
       * we use className only for wrapper
       */
      <div className={cnx(CLASS_NAME, 'cf-form-field', className ?? '')} data-test-id={dataTestId}>
        {/* trimming props which are not needed in the wrapped components */}
        <Comp
          {...otherProps}
          disabled={disabled}
          error={showError ? error : undefined}
        />

        {(!showError && helper != null) && <div className='cf-form-field-helper'>{helper}</div>}
        {(showError && hasErrorMessage) && <p className='cf-form-field-error'>{errorMessage}</p>}
      </div>
    )
  }
}

export default FormFieldHOC
