import { cyrillicRegex } from '@/utils/regex'
import type { InputPreset, InputProps } from './types'

export type Presets = Record<InputPreset, Partial<InputProps>>

export const PRESETS: Presets = {
  withoutCyrillic: {
    onKeyPress: (e) => {
      if (cyrillicRegex.test(e.key)) {
        e.preventDefault()
      }
    }
  }
}
