import type { OnChangeEvent, OnChangeField } from './types'

export const TRANSITION_SPEED_SLOW = 200
export const TRANSITION_SPEED_FAST = 20

export const isStringEmpty = (val?: string | null): boolean => val == null || val === ''
export const isStringNotEmpty = (val?: string | null): boolean => !isStringEmpty(val)

export const isError = (val?: boolean | string | null): boolean => {
  return typeof val === 'boolean' ? val : isStringNotEmpty(val)
}

export const animatePlaceholder = (transitionSpeed: number, cb: () => void): void => {
  setTimeout(() => window.requestAnimationFrame(cb), transitionSpeed)
}

export const isOnChangeByEvent = (onChange: OnChangeField | OnChangeEvent): onChange is OnChangeEvent => {
  return onChange.length === 1
}
