import {
  type FC,
  type FocusEvent,
  type KeyboardEvent,
  type ChangeEvent,
  forwardRef,
  useState,
  useCallback,
  useRef
} from 'react'
import kebabCase from 'lodash-es/kebabCase'

import { cnx } from '@/utils/ui'
import { isError, isStringNotEmpty } from '@/utils'
import type { BaseInputProps, InputProps } from './types'
import { isOnChangeByEvent } from './utils'
import { PRESETS } from './constants'
import CLASS_NAME from './styles'

const _Input = forwardRef<HTMLInputElement, BaseInputProps>(
  ({ value, ...otherProps }, ref) => <input {...otherProps} value={value === null ? '' : value} ref={ref} />
)

const Input: FC<InputProps> = ({ preset, ...otherProps }) => {
  const {
    className,
    dataTestId,
    defaultValue,
    disabled,
    endAdornment,
    error,
    id,
    InputComponent,
    inputProps,
    inputRef: _inputRef,
    label,
    maxLength,
    onFocus: _onFocus,
    onBlur: _onBlur,
    onChange: _onChange,
    onKeyDown: _onKeyDown,
    placeholder,
    startAdornment,
    value,
    passInitialPlaceholder = false,
    ...rest
  } = { ...(preset != null && PRESETS[preset]), ...otherProps }

  const _value = (value ?? defaultValue)?.toString()

  const [focused, setFocused] = useState(false)
  const [withText, setWithText] = useState(isStringNotEmpty(_value))
  const defaultInputRef = useRef<HTMLInputElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)
  const adornmentRef = useRef<HTMLDivElement>(null)
  const inputRef = _inputRef ?? defaultInputRef

  const ComponentToRender = InputComponent ?? _Input

  /**
   * requirement -> do not show placeholder until focused if label is present
   * withText is intentionally not in declaration.
   * We need to check if placeholder was intended disregarding the input in onBlur
   */
  const isPlaceholderMayBeShown = isStringNotEmpty(placeholder) && (focused || label == null)

  /**
   * _value and withText are both checked together to cover cases for controlled and uncontrolled inputs
   */
  const labelFocusedStyle = isStringNotEmpty(label) && (focused || withText || isStringNotEmpty(_value?.toString()))
    ? 'with-label-focused'
    : ''

  const _placeholder = (isPlaceholderMayBeShown && !withText) ? placeholder : ''

  // ========================================== //
  //                   HANDLERS                 //
  // ========================================== //

  const onFocus = useCallback((e: FocusEvent<HTMLInputElement>) => {
    _onFocus?.(e)
    setFocused(true)
  }, [_onFocus])

  const onKeyDown = useCallback((e: KeyboardEvent<HTMLInputElement>) => {
    _onKeyDown?.(e)
    setWithText(isStringNotEmpty(e.currentTarget.value))
  }, [_onKeyDown])

  const onChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    if (_onChange == null) {
      return
    }

    if (isOnChangeByEvent(_onChange)) {
      _onChange(e)
    } else {
      _onChange(e.target.getAttribute('id') ?? '', e.target.value)
    }
  }, [_onChange])

  const onBlur = useCallback((e: FocusEvent<HTMLInputElement>) => {
    _onBlur?.(e)
    setFocused(false)
    setWithText(isStringNotEmpty(e.target.value))
  }, [_onBlur, isPlaceholderMayBeShown, label, placeholder])

  const props = {
    ...rest,
    ...inputProps,
    ...(maxLength != null ? { maxLength } : undefined),
    /* if a component is passed directly then
    * it should manage its ref separately as it might be of a different type.
    */
    ...(InputComponent == null ? { ref: inputRef } : undefined),

    className: 'cf-input',
    'data-test-id': dataTestId ?? kebabCase(id),
    disabled,
    id,
    name: id,
    onBlur,
    onChange,
    onFocus,
    onKeyDown,
    defaultValue,
    /**
    * null is not valid for input; but undefined is valid
   */
    value: value === null ? '' : value
  }

  // ========================================== //

  return (
    <div
      ref={containerRef}
      className={cnx(
        CLASS_NAME,
        focused ? 'focused' : '',
        (isPlaceholderMayBeShown && !withText) ? 'with-placeholder' : '',
        startAdornment != null ? 'with-adornment' : '',
        isStringNotEmpty(label) ? 'with-label' : '',
        labelFocusedStyle,
        isError(error) ? 'with-error' : '',
        disabled === true ? 'disabled' : '',
        'cf-input-container',
        className
      )}
      tabIndex={0}
    >
      {startAdornment != null && (
        <div
          ref={adornmentRef}
          className={cnx('cf-start-adornment', 'cf-input-adornment')}
        >
          {startAdornment}
        </div>
      )}

      <ComponentToRender
        {...props}
        {...(passInitialPlaceholder ? { initialPlaceholder: placeholder } : undefined)}
        placeholder={_placeholder}
      />

      {label != null && (
        <label
          className='cf-input-label'
          htmlFor={id}
        >
          {label}
        </label>
      )}

      {endAdornment != null && (
        <div className={cnx('cf-end-adornment', 'cf-input-adornment')}>{endAdornment}</div>
      )}
    </div>
  )
}

export default Input
