import isObject from './isObject'

function deepContainsTruthy (val: unknown): boolean {
  if (isObject(val)) {
    let res = false
    const keys = Object.keys(val) as Array<keyof typeof val>

    for (const k of keys) {
      if (res) {
        return res
      }

      res = deepContainsTruthy(val[k])
    }

    return res
  }

  if (Array.isArray(val)) {
    let res = false
    for (let i = 0; i < val.length; i++) {
      if (res) {
        return res
      }

      res = deepContainsTruthy(val[i])
    }

    return res
  }

  return Boolean(val)
}

export default deepContainsTruthy
